.chat_window {
    width: 44vw;
    display: flex;
    justify-content: flex-end;
    flex-direction: column;
    background-color: #fff;
    border-bottom: 1.5px solid #2da785;
}

.newMessageForm {
    display: flex;
}

.newMessageInput {
    width: 31vw;
    padding: 1vw;
    border: 1.5px solid #2da785;
}

.sendMessageButton {
    width: 3.28vw;
    height: 3.28vw;
    border: 1.5px solid #2da785;
    background-color: #fff;
}