.userWrapper {
    width: 30vw;
    display: flex;
    justify-content: space-between;
    margin: 1vh;
    border: 1.5px solid #2da785;
}

.userDescription {
    width: 10vw;
    text-align: left;
}

.userPhotoAndDescription {
    display: flex;
}

.userAva {
    margin-right: 2vw;
}

.followButton {
    border: 1.5px solid #2da785;
    background-color: #fff;
    padding: 1vh;
    margin: 2vh 1vw 0 0;
}

.followButton:hover {
    cursor: pointer;
}