.post {
    display: flex;
    flex-direction: column;
    width: 45vw;
    margin-bottom: 2vh;
    border: 1.5px solid #2da785;
    padding: 1vw;
}

.user_info {
    display: flex;
    align-items: center;
}

img {
    width: 5vw;
    border-radius: 50%;
    margin-bottom: 1vw
}

.name {
    margin-left: 2vw;
}

.likeButtonWrapper {
    display: flex;
    justify-content: flex-end;
}

.likeButton {
    width: 2vw;
    height: 2vw;
    margin-right: 1vw;
}

.likeButton:hover {
    cursor: pointer;
}

