.chatLink {
    text-decoration: none;
    color: #000;
}

.chat_wrapper {
    display: flex;
    flex-direction: column;
    width: 25vw;
    border-bottom: 1.5px solid #79c8b1;
    border-right: 1.5px solid #79c8b1;
    padding: 0.5vw 0 0.5vw 3vw;
    background-color: #fff;
}

.user_info {
    display: flex;
    align-items: center;
}

img {
    width: 5vw;
    border-radius: 50%;
}

.lastMessage {
    width: 20vw;
}